import { Component, ViewChild, OnInit } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { MatSort, Sort } from '@angular/material/sort';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { NGXLogger } from 'ngx-logger';
import { TranslateService } from '@ngx-translate/core';
import { UserManagementService } from 'src/app/shared/service/user-management.service';
import { UserDetails } from 'src/app/shared/model/UserDetails';
import { ActivatedRoute, Router, ActivatedRouteSnapshot } from '@angular/router';
import { constants } from 'os';
import { Constants } from 'src/app/shared/utils/constants';
import { ErrorConstants } from 'src/app/shared/utils/errorconstants';
import { ErrorLogging } from 'src/app/shared/model/ErrorLogging';
import { AuthResponse } from 'src/app/shared/model/AuthResponse';
import { AuthAttributes } from 'src/app/shared/model/AuthAttributes';
import { LocationStrategy } from '@angular/common';
import { MediaObserver, MediaChange } from '@angular/flex-layout';
import { Subscription } from 'rxjs';
import { Toc } from 'src/app/shared/model/Toc';
import { Auth } from 'aws-amplify';
import { environment } from 'src/environments/environment';

/**
 * @title Pull TIS users details from from server and display on UI
 */

@Component({
  selector: 'app-userlist',
  templateUrl: './userlist.component.html',
  styleUrls: ['./userlist.component.css']
})
export class userListComponent implements OnInit {
  /** List of columns for search details. */
  displayedColumns: string[] = Constants.COLUMN_DETAILS_USERLIST_PAGE;
  /** Array object to hold users details returned from API and bind into DataTable. */
  usersDetails: UserDetails[] = [];
  /** To specify default language in which the form will appear. */
  private _defaultLang: string;

  /** Handle error messages returned from service. */
  errorMessagelogging: ErrorLogging;
  /**
   * Accessor for the defaultLang property
   * @return Current value of the language property.
   */
  public get defaultLang(): string {
    return this._defaultLang;
  }

  /**
   * Mutator for the defaultLang property
   * @param language New value of the language property.
   */
  public set defaultLang(value: string) {
    this._defaultLang = value;
  }

  /** MatTableDataSource object to bind with DataTable datasource in UI. */
  userDetailsdataSource: MatTableDataSource<any>;

  /** Paginator to manage paging in datatable. */
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;

  /** MatSort to manage sort in datatable. */
  @ViewChild(MatSort, { static: false }) sort: MatSort;

  /** Set default page size of Paginator. */
  defaultPageSize: number;

  /** Page size options. */
  pageSizeOptions: number[];

  /** Member variable to check and manage user does not exist popup. */
  showPopupIfUserDoesNotExists: boolean = true;

  /** Supported application languages. */
  lang: string[] = Constants.SUPPORTED_LANGUAGES;

  /**Set flag to hide/display edit user button after validating permissions. */
  hasEditPermissions: boolean = false;

  /**Set flag to hide/display edit user button after validating permissions. */
  hasAddPermissions: boolean = false;

  /**Member variable to store response from resolver service. */
  currentUserInfo: any;

  /** Member variable for storing star hub user TOC. */
  starHubUserToc: string = "";

  /** Member variable for storing star hub user TOC Name. */
  starHubUserTocName: string = "";

  /**Search key for filtering  */
  searchKey: string;

  /** Member variable to store Tocs returned from API and Bind with control. */
  tocs: Toc[] = [];

  /**Member variable to display number of elements on the page. */
  loggedPageSize: string;

  /** Watches for media changes to support responsive changes to content. */
  flexMediaWatcher: Subscription;

  /** Holds current screen width, updated as a result of media change. */
  currentScreenWidth: string = '';

  /** Show/hide toolbar buttons based on screen width.  */
  showToolbarButtons: boolean = true;

  /**
   * Constructor with injected route and NGXLogger.
   * @param ActivatedRoute For managing the routing.
   * @param logger Service that translates messages to other languages.
   * @param userManagementService Service for API calls
   * @param translate Multilingual support
   */
  constructor(private route: ActivatedRoute,
    private router: Router,
    private logger: NGXLogger,
    private userManagementService: UserManagementService,
    private translate: TranslateService,
    private location: LocationStrategy,
    private media: MediaObserver) {
    this.defaultLang = Constants.DEFAULT_LANGUAGE;
    translate.addLangs(this.lang);
    translate.setDefaultLang(this.defaultLang);

    this.flexMediaWatcher = media.media$.subscribe((change: MediaChange) => {
      if (change.mqAlias !== this.currentScreenWidth) {
        this.currentScreenWidth = change.mqAlias;
        this.setupTable();
      }
    });
  }


  /** Define columns to be displayed as screen width changes. */
  private setupTable() {
    this.displayedColumns = Constants.COLUMN_DETAILS_USERLIST_PAGE;
    if (this.currentScreenWidth == Constants.SCREEN_SIZE_VERY_SMALL ||
      this.currentScreenWidth == Constants.SCREEN_SIZE_SMALL) {
      if (this.hasEditPermissions) {
        this.displayedColumns = Constants.COLUMN_DETAILS_USERLIST_SMALL_PAGE;
      }
      else {
        this.displayedColumns = Constants.COLUMN_DETAILS_USERLIST_SMALL_PAGE_NO_EDITPERMISSION;
      }
      this.showToolbarButtons = false;
    } else {
      if (this.hasEditPermissions) {
        this.displayedColumns = Constants.COLUMN_DETAILS_USERLIST_PAGE;
      } else {
        this.displayedColumns = Constants.COLUMN_DETAILS_USERLIST_PAGE_NO_EDITPERMISSION;
      }
      this.showToolbarButtons = true;
    }
  }

  /**
   * Implement OnInit's `ngOnInit` method.
   */
  ngOnInit() {
    //Reading data for current user info from resolver to find star hub user info.
    this.currentUserInfo = this.route.snapshot.data[Constants.LANDING_PAGE_RESOLVER_FIELD];
    let authResponse = new AuthResponse(this.currentUserInfo);
    this.hasAddPermissions = authResponse.hasAddPermissions;
    this.hasEditPermissions = authResponse.hasEditPermissions;
    this.defaultPageSize = Constants.DEFAULT_PAGE_SIZE;
    this.starHubUserToc = authResponse.defaultToc;

    //Hide/unhide Action edit button based on user's permission  
    this.setupTable();

    //Checking if page size is set on page load.
    let selectedPageSize = localStorage.getItem('loggedPageSize');
    //This would be null when the application loads for the first time.
    if (null != selectedPageSize) {
      this.loggedPageSize = selectedPageSize;
    } else {
      //Excepting the selected value by user after coming back
      // from different components and setting that value.
      this.loggedPageSize = this.defaultPageSize.toString();
    }

    this.getTocs();
    this.getUsersDetails(this.starHubUserToc);
    this.pageSizeOptions = Constants.PAGE_SIZE_OPTION;
  }

  /**Method to clear the filter from the search box. */
  onSearchClear() {
    this.searchKey = "";
    this.applyFilter();
  }

  /**Applying filter to keep search string in lowercase. */
  applyFilter() {
    this.userDetailsdataSource.filter = this.searchKey.trim().toLowerCase();
  }

  /**
   * Method to capture user selected sort order
   * and store it in local storage for reference.
   * @param sort 
   */
  sortData(sort: Sort) {
    var changedPageSize;
    if (null != localStorage.getItem('loggedPageSize')) {
      changedPageSize = localStorage.getItem('loggedPageSize');
    }
    //Clearing local storage to retain only last selected column header sort order.
    localStorage.clear();

    if (sort.active == Constants.COLUMN_HEADER_FIRSTNAME) {
      localStorage.setItem(Constants.COLUMN_HEADER_FIRSTNAME, sort.direction);
    } else if (sort.active == Constants.COLUMN_HEADER_LASTNAME) {
      localStorage.setItem(Constants.COLUMN_HEADER_LASTNAME, sort.direction);
    } else if (sort.active == Constants.COLUMN_HEADER_STATUS) {
      localStorage.setItem(Constants.COLUMN_HEADER_STATUS, sort.direction);
    } else if (sort.active == Constants.COLUMN_HEADER_EMPLOYEED_ID) {
      localStorage.setItem(Constants.COLUMN_HEADER_EMPLOYEED_ID, sort.direction);
    }

    if (null != changedPageSize) {
      localStorage.setItem('loggedPageSize', changedPageSize);
    }

  }

  /**
   * Method to capture page size event change and store this in local stoage
   * to display same value after routing to different pages.
   * @param event - Event that captures page size selected by the user.
   */
  public getPageSize(event: PageEvent) {
    if (event.pageSize != this.defaultPageSize) {
      localStorage.setItem('loggedPageSize', event.pageSize.toString());
    }
  }

  /**
   * Method to handle Add User button click event.
   */
  addTISUserClick() {
    this.router.navigate([Constants.USER_FORM]);
  }

  /**
  * Method to navigate to landing page help.
  */
  userHelpClick() {
    localStorage.setItem('sourcePageOfHelp', Constants.USER_LIST_FORM);
    //Navigate to the landing page help.
    window.open(Constants.USER_HELP_FORM, "umUserHelp");
  }

  /** 
   * Fetch TOCs from API.
   */
  getTocs() {
    this.userManagementService.getTocs().subscribe
      (
        (data: any) => {
          if (data != null) {
            if (data.length > 0) {
              this.tocs = data;
              let filterResult: any = this.tocs.filter(u =>
                u.internalTocCode == this.starHubUserToc);
              if (filterResult != null && filterResult.length > 0) {
                let tocObject: Toc = <Toc>filterResult[0];
                this.starHubUserTocName = tocObject.toc;
              }
            }
          } else {
            this.tocs = [];
          }
        },
        (error: any) => {
          this.logger.error(error);
        }
      );
  }

  /**
   * Fetch UsersDetails from API.
   * @param getUsersToc - Toc internal code for which list of users will be fetched
   */
  getUsersDetails(getUsersToc: string) {
    this.usersDetails = [];
    this.userManagementService.getUsersDetails(getUsersToc).subscribe
      (
        (data: UserDetails[]) => {
          if (data != null && data.length > 0) {
            // Typecast returned object to UserDetails
            this.usersDetails = data as UserDetails[];
            /*
            Iterate through API returned user details and set Status to deleted/active
            based in termination date field value
            */
            this.usersDetails.forEach(childobject => {
              if (childobject.terminationDate == null) {
                childobject.status = Constants.USER_ACTIVE;
              }
              else {
                childobject.status = Constants.USER_DELETED;
              }
            });
            //Default sorting on Last name in ascending order
            this.usersDetails.sort((a, b) => (a.lastName > b.lastName) ? 1 : -1);
          }

          // Final binding of returned users details with Data Table
          this.userDetailsdataSource = new MatTableDataSource<UserDetails>(this.usersDetails);
          // Set paging in datatable
          this.userDetailsdataSource.paginator = this.paginator;
          //Set sorting in datatable.
          this.userDetailsdataSource.sort = this.sort;
        },
        (error: any) => {
          this.userDetailsdataSource = new MatTableDataSource<UserDetails>(this.usersDetails);
          this.logger.error(error);
        }
      );
  }

  /**
   * Capture data table pagesize changed event.
   * @param value - Page size changed event
   */
  pageEvent(value) {

  }

  /*
   * Method to control auth logout. User will be redirected to 
   * login page. Also, all local storage/cache for  
   * page size would be cleared.
   */
  logout() {
    Auth.signOut().then(x => {
      //Clearing the local storage when the user has signed out.
      localStorage.clear();
      //Redirecting the user to login page after validating that
      //the current user is not logged in.
      const redirectUrlParams = window.location.href;
      window.location.href = environment.auth_ui_url + '?redirect=' + redirectUrlParams;
    });
  }

  /**
   * Handle action edit button event and re-direct ro edit TIS user page
   * @param selectedEmployeeId - Employee id of selected row for edit
   * @param selectedToc - TOC for selected row for edit
   */
  getUserDetails(selectedToc: string, selectedEmployeeId: string) {
    this.userManagementService.getUserDetails(selectedToc, selectedEmployeeId).subscribe
      (
        (data: UserDetails[]) => {
          let userDetails: UserDetails[] = [];
          if (data != null && data.length > 0) {
            // Typecast returned object to UserDetails
            userDetails = data as UserDetails[];
            this.router.navigate([
              Constants.USER_FORM, Constants.EDIT, userDetails[0].toc, userDetails[0].employeeId]);
          }
        },
        (error: any) => {
          this.logger.error(error);
          this.errorMessagelogging = error;
          if (this.errorMessagelogging.errorMessage.includes('$')) {
            if (this.errorMessagelogging.errorMessage.split('$')[0].includes(
              Constants.HTTP_STATUS_PAGE_NOT_FOUND.toString())) {
              this.showPopupIfUserDoesNotExists = false;
              alert(ErrorConstants.MESSAGE_USERID_CANNOT_EDITED);
            }
          }
        }
      );
  }
}